import React from 'react';
import {Link} from 'react-router-dom';
import AuthenService from '../services/AuthenService';
import Token from '../services/Token'
const HOME = "http://localhost:3000";
const date = new Date().getTime();

const HeaderComponent = () => {

    const handleLogout = () => {
        AuthenService.logout(Token.info).then(() => {
            window.location.reload();
        }).catch(() => {
            alert("Đăng xuất thất bại")
        })
    }


    return (
        <div>
            <header>
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                    <Link className="navbar-brand" to={HOME} style={{
                        marginLeft: 50
                    }}>Trang Chủ</Link>

                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <Link className="nav-link" to={"/dang-ky-mon-hoc"}>Đăng ký môn học</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/trao-doi-mon-hoc"}>Trao đổi môn học</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/thong-tin-trao-doi-mon-hoc"}>Thông tin trao đổi môn học</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/thoi-khoa-bieu"}>Thời khóa biểu</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to = {"/xem-diem"}>Xem điểm</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/thong-tin-sinh-vien"}>Xem thông tin</Link>
                            </li>
                            <li className="nav-item">
                                {Token.info != null && Token.info.expiration < date ? (
                                <Link className="nav-link" to={"/dang-nhap"}>Đăng nhập</Link>
                                ) :  (
                                    <Link className="nav-link" to={"/trang-chu"} onClick = {() => {

                                    }}>Xin chao {Token.info.username}</Link>
                                    )}
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link"  to={"/dang-ky"}>Đăng ký</Link>
                            </li>
                        </ul>
                    </div>
                </nav>
            </header>
        </div>
    )
}
export default HeaderComponent;